@import url(https://fonts.googleapis.com/css?family=Roboto+Mono:300&display=swap);
body {
  margin: 0;
  font-family: 'Roboto Mono', Courier, monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

/* Custom Styling */
h1,
h2,
h3,
h4,
h5 {
  margin: 0;
  padding: 0.5rem;
  font-weight: bold;
}

h1 {
  font-size: 1.75rem;
}

h2 {
  font-size: 1.5rem;
}

h3 {
  font-size: 1.25rem;
}

h4 {
  font-size: 1rem;
}

h5 {
  font-size: 0.75rem;
}

p {
  margin: 0;
  padding: 0;
}

.form-button {
  border: 1px solid;
  border-color: lightblue;
  border-radius: 0.25rem;
  padding: 0.5rem 1rem;
  margin: 0 0.25rem;
  text-decoration: none;
  background: #0069ed;
  color: #ffffff;
  font-family: sans-serif;
  font-size: 1rem;
  cursor: pointer;
  text-align: center;
  -webkit-appearance: none;
  -moz-appearance: none;
}

/* react-particles-js */
.particles {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
}

.nav {
  display: flex;
  padding: 1rem 2rem;
}

.nav-section {
  flex: 1 1;
  display: flex;
  justify-content: center;
}

.nav-section:first-child {
  justify-content: flex-start;
}

.nav-section:last-child {
  justify-content: flex-end;
}

.nav-title {
  flex-direction: column;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.nav-item:hover {
  text-decoration: underline;
  cursor: pointer;
  color: #5a5a5a;
}

.logo-img {
  height: 120px;
  width: 120px;
}

.signin--container {
  padding: 3rem;
  display: flex;
  justify-content: center;
  text-align: center;
}

.signin--form--container {
  width: 40vw;
  max-width: 600px;
}

.signin--form--input {
  width: 80%;
}

.ba {
  border-style: solid;
  border-width: 1px;
}

.b--black {
  border-color: #000;
}

.b--transparent {
  border-color: transparent;
}

.db {
  display: block;
}

.dib {
  display: inline-block;
}

.b {
  font-weight: bold;
}

.fw6 {
  font-weight: 600;
}

.input-reset {
  -webkit-appearance: none;
     -moz-appearance: none;
}

.input-reset::-moz-focus-inner {
  border: 0;
  padding: 0;
}

.lh-copy {
  line-height: 1.5;
}

.link {
  text-decoration: none;
  -webkit-transition: color .15s ease-in;
  transition: color .15s ease-in;
}

.link:link, .link:visited {
  -webkit-transition: color .15s ease-in;
  transition: color .15s ease-in;
}

.link:hover {
  -webkit-transition: color .15s ease-in;
  transition: color .15s ease-in;
}

.link:active {
  -webkit-transition: color .15s ease-in;
  transition: color .15s ease-in;
}

.link:focus {
  -webkit-transition: color .15s ease-in;
  transition: color .15s ease-in;
  outline: 1px dotted currentColor;
}

.black-80 {
  color: rgba(0, 0, 0, .8);
}

.black {
  color: #000;
}

.bg-transparent {
  background-color: transparent;
}

.hover-white:hover {
  color: #fff;
}

.hover-white:focus {
  color: #fff;
}

.hover-bg-black:hover {
  background-color: #000;
}

.hover-bg-black:focus {
  background-color: #000;
}

